.create-acc {
  width: 384px;
  background: #fff;
  border-radius: 5px;
  box-shadow: (0 2px 25px rgba(0, 0, 0, 0.25));
  align-self: center;
  padding: 48px 32px 48px 32px;
  .title {
    color: #262626;
    font-size: 20px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    letter-spacing: 0.3px;
    line-height: 28px;
    margin-bottom: 20px;
  }
  .input {
    height: 40px;
    font-size: 16px;
    line-height: 24px;
  }
  .item {
    margin-bottom: 12px;
    font-size: 14px;
    font-family: Roboto;
  }

  .checkbox-agree {
    margin-top: 12px;
    font-size: 14px;
    text-align: start;
  }
  .submit-button__box {
    margin-bottom: 10px;
  }
  .submit-button {
    width: 100%;
    height: 42px;
    font-size: 16px;
  }
  .login-link {
    font-family: Roboto;
    color: #8c8c8c;
    font-size: 12px;
    line-height: 20px;
    .sign-in {
      color: #1890ff;
    }
  }
}
