.edit-profile {
  width: 384px;
  background: #fff;
  border-radius: 5px;
  box-shadow: (0 2px 25px rgba(0, 0, 0, 0.25));
  align-self: center;
  padding: 48px 32px 48px 32px;
  .title {
    color: #262626;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    letter-spacing: 0.3px;
    line-height: 28px;
    margin-bottom: 20px;
  }
  .input {
    height: 40px;
  }
  .item {
    margin-bottom: 12px;
  }
  .button {
    font-family: Roboto;
    font-size: 16px;
    height: 40px;
    width: 100%;
  }
}
